import { Container, Row, Col } from "react-bootstrap";
import Accordion from 'react-bootstrap/Accordion';

export const Aviso = () =>{
    return(


      <section className="historia" id="aviso">

      <Container>
         
 

        <Accordion defaultActiveKey="0">
        <Accordion.Item eventKey="0">
          <br />
        <h1>AVISOS</h1>
          <Accordion.Header> <h3>Sobre Cortes</h3> </Accordion.Header>
          <Accordion.Body>
           <p class="parrafo1">Estimado usuarios se les informa que a partir del mes de febrero se harn
              cortes permanentes aquellos usuarios que tiene un saldo pendiente de
            <b> 2 (meses)</b> en delante, se le pide visitar la oficina de <b>Atencin al Cliente</b>  en el edificio de 
               Aguas de Danl a realizar la cancelacin de su recibo para evitar el corte de su servicio y cargos por reconexin de lps. 358.00.
                Ofreciendo tambin en caso que no pueda realizar su pago de toda la mora, hacer plan de pago con una prima de 50% y el saldo 
                restante en cuotas de cuerdo a las polticas de cobranza de Aguas de Danl.</p>
          </Accordion.Body>
        </Accordion.Item>


        <Accordion.Item eventKey="1">
          <Accordion.Header><h3>Aviso de alto consumo: </h3></Accordion.Header>
          <Accordion.Body>
          <p class="parrafo1">
          Usuario si usted presenta tarifa elevada por su consumo de agua potable de acuerdo al tarifa fija, se le comunica que se les 
          est atendiendo en la oficina de Atencin al Cliente en el Edificio de Aguas de Danli y as presentar su reclamos y poderle 
          apoyar con su revisin del medidor e inspecciones de fugas.
          </p>
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="2">
          <Accordion.Header><h3>Aviso por derrame: </h3></Accordion.Header>
          <Accordion.Body>
           <p class="parrafo1">
           Seor Usuario se le pide evitar el derrame del servicio de agua potable en su vivienda, de esta manera evita 
           el llamado de atencin, el corte del servicio,  multa por derrame y el pago de facturas elevadas por desperdicio.
           </p>
          </Accordion.Body>
        </Accordion.Item>

     

          <Accordion.Item eventKey="4">
          <Accordion.Header><h3>En caso de Interrupcin de la Energa Elctrica por EEHH: </h3></Accordion.Header>
          <Accordion.Body>
           <p class="parrafo1">
           Le avisamos que el Servicio de agua potable se les suministrara hasta el siguiente da de acuerdo a su horario.
           </p>
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="5">
          <Accordion.Header><h3>Aviso por Reparaciones de Tubera:  </h3></Accordion.Header>
          <Accordion.Body>
           <p class="parrafo1">
           Seor Usuario debido a problemas en tubera en el barrio o colonia, no se les estar suministrando
            el servicio de agua potable en este da. La cuadrilla de Operacin y Mantenimiento 
           se encuentra en labores de reparacin, una vez finalizados las reparaciones se suministrar el servicio de
            agua potable de acuerdo a su horario en horas tarda.
           </p>
          </Accordion.Body>
        </Accordion.Item>

        <Accordion.Item eventKey="3">
          <Accordion.Header><h3>Aviso para ingresar a las oficinas de Agua de Danl:  </h3></Accordion.Header>
          <Accordion.Body>
           <p class="parrafo1">
           Seor Usuario es obligatorio el uso de Mascarilla, aplicacin de gel antibacterial y/o alcohol, uso del pediluvio 
           y la toma de temperatura y recordarle mantener el distanciamiento fsico de las personas de 1,5 metros.
           </p>
          </Accordion.Body>
        </Accordion.Item>
        <br />
        <p><b>Para llamar cualquier emergencia puedes contactarnos a los telfonos 2763-5210 y 2763-5241, celular va whatsapp 9420-3207.</b></p>
      </Accordion>
      <br />
      <br />
      <br />





      </Container>
 
  </section>
      
     



  
 
    )
}